function darkMode(){

    const prefiereDarkMode = window.matchMedia('(prefers-color-scheme: dark)'); //sirve para leer las preferencias de la computadora del usuario tema dark o light
    //console.log(prefiereDarkMode.matches);
    const btndark = document.getElementById('drkmode');
    const light = document.getElementById('lightmode');
    
    if(prefiereDarkMode.matches){
        document.body.classList.add('dark-mode');
        btndark.classList.add('oculto');
    }else{
        document.body.classList.remove('dark-mode');
        light.classList.add('oculto');
    }

    // prefiereDarkMode.addEventListener('change', function(){
    //     if(prefiereDarkMode.matches){
    //         document.body.classList.add('dark-mode');
    //     }else{
    //         document.body.classList.remove('dark-mode');
    //     }
    // });
    
    const botonDarkMode = document.querySelector('.dark-mode-boton');

    botonDarkMode.addEventListener('click', function(){
        document.body.classList.toggle('dark-mode');
        btndark.classList.toggle('oculto');
        light.classList.toggle('oculto');

        //guardar el modo en localStorage
    if(document.body.classList.contains('dark-mode')){
        localStorage.setItem('dark-mode','true');
    }else{
        localStorage.setItem('dark-mode','false');
    }
    });

    //obtenemos el modo actual
    if(localStorage.getItem('dark-mode') === 'true'){
        document.body.classList.add('dark-mode');
        btndark.classList.add('oculto');
        light.classList.remove('oculto');
    }else{
        document.body.classList.remove('dark-mode');
        light.classList.add('oculto');
        btndark.classList.remove('oculto');
    }
    
}

function eventListeners(){
    const mobileMenu = document.querySelector('.mobile-menu');

    mobileMenu.addEventListener('click',navegacionResponsive);

    //Muestra campos condicionales
    const metodoContacto = document.querySelectorAll('input[name="contacto[contacto]"]'); //selecciona todos los inputs que tenga ese valor en su campo de name
    metodoContacto.forEach(input => input.addEventListener('click',mostrarFormaContacto)); //como es un querySelectorAll no permite asignarle directamente un event listener, por lo que necesitamos iiterarlo y asignarselo uno a uno
}

function navegacionResponsive(){
    //console.log('desde navegacionResponsive');
    const navegacion = document.querySelector('.navegacion');

    if(navegacion.classList.contains('mostrar')){
        navegacion.classList.remove('mostrar');
    }else{
        navegacion.classList.add('mostrar');
    }

    //navegacion.classList.toggle('mostrar'); toggle hace exactamente lo mismo que el if de arriba
}

