function borraMensaje() {
    const mensajeConfirm = document.querySelector('.alerta');
    if(mensajeConfirm !== null){
        setTimeout(function() {
            const padre = mensajeConfirm.parentElement;
            padre.removeChild(mensajeConfirm);
            history.pushState(null, "", "/bienesraices/admin/index.php");
        }, 2500);
        console.log("Hay mensaje de error");
    }else {
        console.log("No hay mensaje de error");
    }
}

function mostrarFormaContacto(e){
    const contactoDiv = document.querySelector('#contacto');
    //contactoDiv.textContent = 'Diste click';
    //console.log(e);
    if(e.target.value === 'telefono'){
        contactoDiv.innerHTML = `
        <label for="telefono">Número de telefono</label>
        <input type="tel" placeholder="Tu telefono" id="telefono" name="contacto[telefono]" required>

        <p>Elija la fecha y la hora para la llamada </p>

                <label for="fecha">Fecha</label>
                <input type="date" id="fecha" name="contacto[fecha]" required>

                <label for="hora">Hora</label>
                <input type="time" id="hora" min="09:00" max="18:00" name="contacto[hora]" required>
        `;
    }else{
        contactoDiv.innerHTML = `
        <label for="email">E-mail</label>
        <input type="email" placeholder="Tu e-mail" id="email" name="contacto[email]" required>
        `;
    }
}

